<template>
    <div class="container">
        <div class="row mt-4 mb-4">
            <div class="reel-container col-md-4" ref="reels" v-for="(reel, index) in reels" v-bind:key="index">
                <slot-reel :reel="reel"></slot-reel>
            </div>
        </div>
        <div class="row">
            <div class="mb-2 flex flex-grow-1 text-center" v-if="choices.length > 0">{{ choices }}</div>
            <button class="btn btn-primary btn-block" @click="retrieveData">start</button>
        </div>
    </div>
</template>
<script>
    import axios from 'axios';

    const next =
        window.requestAnimationFrame ||
        window.webkitRequestAnimationFrame ||
        window.mozRequestAnimationFrame ||
        window.msRequestAnimationFrame ||
        window.oRequestAnimationFrame ||
        function (cb) {
            window.setTimeout(cb, 1000 / 60);
        };

    export default {
        props: {
            reelAmount: Number,
            rows: Number,
            symbolsAmount: {
                type: Number,
                default: 25
            }
        },
        data() {
            return {
                opts: null,
                startedAt: null,
                choices: []
            };
        },
        computed: {
            endpoint() {
                return 'https://casino.nomadnt.com/game/1/spin?api_token=uhg8usBbZ6r0v9Yu0pW59lz3DRTP0jxr1XK9COrFdCE9SqGfBXu4x0P8vduuWN4x6Gf1bY1H6aSugRGj';
            },
            reels() {
                let reels = [];
                // eslint-disable-next-line for-direction
                for (let i = 0; i < this.reelAmount; i++) {
                    reels.push({items: [...Array(this.symbolsAmount).keys()]});
                }
                return reels;
            },
            symbols() {
                return Array.from(Array(this.symbolsAmount).keys());
            }
        },
        methods: {
            animate(timestamp) {
                if (this.startedAt == null) {
                    this.startedAt = timestamp;
                }

                const timeDiff = timestamp - this.startedAt;

                this.opts.forEach(opt => {
                    if (opt.isFinished) {
                        return;
                    }

                    const timeRemaining = Math.max(opt.duration - timeDiff, 0);
                    const power = 3;
                    const offset =
                        (Math.pow(timeRemaining, power) / Math.pow(opt.duration, power)) *
                        opt.startOffset;

                    // negative, such that slots move from top to bottom
                    const pos = -1 * Math.floor((offset + opt.finalPos) % opt.height);

                    opt.el.style.transform = "translateY(" + pos + "px)";

                    if (timeDiff > opt.duration) {
                        opt.isFinished = true;
                    }
                });

                if (this.opts.every(o => o.isFinished)) {
                    this.opts = null;
                    this.startedAt = null;
                } else {
                    next(this.animate);
                }
            },
            getShuffledSymbols() {
                let array = JSON.parse(JSON.stringify(this.symbols));
                let counter = this.symbols.length;

                while (counter > 0) {
                    let index = Math.floor(Math.random() * counter);
                    counter--;
                    let temp = array[counter];
                    array[counter] = array[index];
                    array[index] = temp;
                }

                return array;
            },
            retrieveData() {
                axios.get(this.endpoint)
                    .then(({data}) => {
                        this.choices = data.outcome;
                        this.spin();
                    })
            },
            spin() {
                if (this.opts) return;

                this.opts = this.reels.map((data, i) => {
                    const slot = this.$refs.reels[i];
                    const choice = this.choices[i];
                    console.log(choice);

                    const opts = {
                        el: slot.querySelector(".slot__wrap"),
                        finalPos: choice * 180,
                        choice: choice,
                        startOffset: 2000 + Math.random() * 500 + i * 500,
                        height: data.items.length * 180,
                        duration: 3000 + i * 700, // milliseconds
                        isFinished: false
                    };

                    return opts;
                });

                next(this.animate);
            }
        }
    }
</script>